<template>
  <div>
    <section class="content">
      <el-form label-width="110px" label-position="left" :model="deviceParam">
        <h4 style="text-align: left;margin-top: 0"></h4>
        <div class="add-appdiv card-width">
          <el-row>
            <el-col :span="12">
              <el-form-item label="ID" align="left" style="margin: 0">
                {{deviceMonitor.devId ? deviceMonitor.devId : '--'}}
              </el-form-item>
              <el-form-item label="" align="left" style="margin: 0">{{devForm}}</el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" align="left" style="margin: 0">
                {{deviceMonitor.devName ? deviceMonitor.devName : '--'}}
              </el-form-item>
              <el-form-item label="" align="left" style="margin: 0">{{devType}}</el-form-item>
            </el-col>
          </el-row>
        </div>
        <div class="add-appdiv card-width">
          <el-row>
            <el-col :span="12">
              <h4 style="text-align: left;margin-top: 0"></h4>
              <el-form-item label="" align="left" style="margin: 0">
                {{deviceMonitor.version ? deviceMonitor.version : '--'}}
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <h4 style="text-align: left;margin-top: 0"></h4>
              <el-form-item label="" align="left" style="margin: 0">
                <el-button type="primary" size="medium" @click="runStartDevice = true"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </div>

        <h4 style="text-align: left;margin-top: 0"></h4>
        <div class="add-appdiv card-width">
          <el-row>
            <el-col :span="12">
              <el-form-item label="" align="left" style="margin: 0"><span
                v-bind:class="boardParam.temperature > 80 ? 'text-red' : boardParam.temperature > 50 ? 'text-yellow' :'text-green'">
                  {{boardParam.temperature != null ? boardParam.temperature : '--'}}C
                </span>
              </el-form-item>
              <el-form-item label="cpu" align="left" style="margin: 0"><span
                v-bind:class="boardParam.cpu > 0.8 ? 'text-red' : boardParam.cpu > 0.5 ? 'text-yellow' :'text-green'">
                  {{boardParam.cpu != null ? (boardParam.cpu * 100).toFixed(2) : '--'}}%
                </span>
              </el-form-item>
              <el-form-item label="" align="left" style="margin: 0"><span
                v-bind:class="boardParam.memory > 0.8 ? 'text-red' : boardParam.memory > 0.5 ? 'text-yellow' :'text-green'">
                  {{boardParam.memory != null ? (boardParam.memory * 100).toFixed(2) : '--'}}%
                </span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="3G" align="left" style="margin: 0">
                {{deviceParam.rssi3G != null ? deviceParam.rssi3G : '--'}}dbm
              </el-form-item>
              <el-form-item label="" align="left" style="margin: 0">
                {{boardParam.duration != null ? boardParam.duration : '--'}}
              </el-form-item>
              <el-form-item label="" align="left" style="margin: 0">
                {{boardParam.criminalCodeStatus != null ? boardParam.criminalCodeStatus : '--'}}/min()
              </el-form-item>
            </el-col>
          </el-row>
        </div>
        <div class="add-appdiv card-width">
          <el-tabs v-model="activeItem" type="card" @tab-click="handleClick">
            <el-tab-pane v-bind:label="tab.name" v-bind:name="tab.type" v-for="tab in activeName"
                         :key="tab.type"></el-tab-pane>
          </el-tabs>
          <el-row style="margin-left: 20px">
            <el-col :span="12">
              <el-form-item label="" align="left" style="margin: 0"><span
                v-bind:class="subParam.temperature > 80 ? 'text-red' : subParam.temperature > 50 ? 'text-yellow' :'text-green'">
                  {{subParam.temperature != null ? subParam.temperature : '--'}}C
                </span>
              </el-form-item>
              <el-form-item label="cpu" align="left" style="margin: 0"><span
                v-bind:class="subParam.cpu > 0.8 ? 'text-red' : subParam.cpu > 0.5 ? 'text-yellow' :'text-green'">
                  {{subParam.cpu != null ? (subParam.cpu * 100).toFixed(2) : '--'}}%
                </span>
              </el-form-item>
              <el-form-item label="" align="left" style="margin: 0"><span
                v-bind:class="subParam.memory > 0.8 ? 'text-red' : subParam.memory > 0.5 ? 'text-yellow' :'text-green'">
                  {{subParam.memory != null ? (subParam.memory * 100).toFixed(2) : '--'}}%
                </span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" align="left" style="margin: 0">
                {{subParam.power != null ? subParam.power : '--'}}mW
              </el-form-item>
              <el-form-item label="" align="left" style="margin: 0">
                {{subParam.catchCount != null ? subParam.catchCount : '--'}}
              </el-form-item>
              <el-form-item label="" align="left" style="margin: 0">
                {{subParam.duration != null ? subParam.duration : '--'}}
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </el-form>
      <!---->
      <div class="popu">
        <el-dialog title="" :visible.sync="runStartDevice" :width="dialogWidth">
          <section>
            <p style="font-size: 16px;padding-bottom: 20px"></p>
            <el-row>
              <el-col :span="12">
                <el-button @click="restartDevice" type="text" class="left" size="medium" :loading="loading">
                </el-button>
              </el-col>
              <el-col :span="12">
                <el-button @click="runStartDevice = false" type="text" class="right"></el-button>
              </el-col>
            </el-row>
          </section>
        </el-dialog>
      </div>
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        deviceParam: {},
        boardParam: {},
        subParam: {},
        deviceMonitor: {},
        devForm: '--',
        devType: '--',
        loading: false,
        runStartDevice: false,//
        activeItem: 'T',
        activeName: [{name: '', type: 'T'}, {name: '', type: 'M'}, {name: '', type: 'U'}],
        dialogWidth: this.$Is_Pc() ? '380px' : '300px',
        devForms: [{code: "TYPE_MULTI", name: ""}, {code: "TYPE_GSM", name: "GSM "},
          {code: "TYPE_WIFI", name: "WIFI "}, {code: "TYPE_LTE", name: "LTE "},
          {code: "TYPE_CDMA", name: "CDMA "}, {code: "TYPE_WCDMA", name: "WCDMA "},
          {code: "TYPE_TDSCDMA", name: "TDSCDMA "}, {code: "GSM_CDMA_MU_MODE", name: "GSM+CDMA "},
          {code: "GSM_CDMA_WCDMA_MU_MODE", name: "GSM+CDMA+WCDMA "},
          {code: "CDMA_WCDMA_MU_MODE", name: "CDMA+WCDMA "}, {code: "GSM_LTE_MU_MODE", name: "GSM+LTE "}],
        devTypes: [{code: "CON_OUTDOOR_POLE", name: ""}, {code: "CON_OUTDOOR_MOCRO", name: ""}],
        intervalid: null
      }
    },
    //
    beforeDestroy() {
      clearInterval(this.intervalid);
    },
    methods: {
      //
      handleClick(tab, event) {
        this.activeItem = tab.name;
        this.getSubStatus();
      },
      //
      dataTask() {
        if (!this.intervalid) {
          this.intervalid = setInterval(() => {
            this.getDeviceStatus();
          }, 5000);
        }
      },
      //
      restartDevice() {
        this.runStartDevice = false;
        let param = {msgId: "b7518c70", type: 4192, cmd: 4526, moduleID: 255, timestamp: new Date().getTime()};
        this.$emit('openLoading');
        this.$post(param).then((data) => {
          this.$emit('closeLoading');
          if ("000000" === data.code) {
            this.$message({message: '...', type: 'success'});
          }
        }).catch((err) => {
          this.$message.error(err);
          this.$emit('closeLoading');
        });
      },
      //
      getDeviceParam() {
        let param = {msgId: 'b7518c70', type: 4194, cmd: 4521, moduleID: 255, timestamp: new Date().getTime()};
        this.$emit('openLoading');
        this.$post(param).then((data) => {
          this.$emit('closeLoading');
          if ("000000" === data.code) {
            this.deviceMonitor = data.data;
            if (data.data.devType.length > 0) {
              this.devType = this.getDevType(data.data.devForm);
            }
            if (data.data.devForm.length > 0) {
              this.devForm = this.getDevForm(data.data.devType);
            }
            this.$emit('showDialog', false, false);
          }
        }).catch((err) => {
          this.$message.error(err);
          this.$emit('closeLoading');
        });
      },
      getDevForm(val) {
        for (let item of this.devForms) {
          if (val === item.code) {
            return item.name;
          }
        }
      },
      getDevType(val) {
        for (let item of this.devTypes) {
          if (val === item.code) {
            return item.name;
          }
        }
      },
      //
      getDeviceStatus() {
        let param = {msgId: "b7518c70", type: 4194, cmd: 4527, moduleID: 255, timestamp: new Date().getTime()};
        this.$post(param).then((data) => {
          if ("000000" === data.code) {
            this.deviceParam = data.data;
            this.deviceParam.deviceMonitorList.forEach((item) => {
              if ('M' === item.operationType) {
                this.boardParam = item;
              }
            });
            this.getSubStatus();
          }
        }).catch((err) => {
          this.$message.error(err);
        });
      },
      //
      getSubStatus() {
        this.deviceParam.deviceMonitorList.forEach((item) => {
          if (this.activeItem === item.operationType) {
            this.subParam = item;
          }
        });
      }
    },
    mounted() {
      this.getDeviceParam();
      this.getDeviceStatus();
      this.dataTask();
    }
  }
</script>
<style scoped>
  .text-green {
    color: #008000;
  }

  .text-yellow {
    color: #FF6A10;
  }

  .text-red {
    color: #C23531;
  }

  @media screen and (min-width: 320px) and (max-width: 799px) {
    .card-width {
      margin-left: 0;
    }
  }

  @media screen and (min-width: 800px) {
    .card-width {
      margin-left: 20px;
    }
  }
</style>



