<template>
  <div>
    <section class="content">
      <h4 style="text-align: left;margin-top: 0"></h4></section>
    <el-row style="margin:0 0 10px 0">
      <el-col :span="24" align="right">
        <el-button type="primary" @click="getBlack();device = {imsi: ''};runningSetBlack= true" style="margin-top: 10px"
                   size="medium">
        </el-button>
        <el-button type="primary" @click="exportExcel()" style="margin-top: 10px" v-show="list.length > 0"
                   size="medium">Excel
        </el-button>
      </el-col>
    </el-row>
    <div class="block" style="height: 60%;background-color: #33CCFF">
      <el-table :data="list" class="center-block" stripe id="out-table"
                :default-sort="{prop: 'uptime', order: 'descending'}">
        <el-table-column align="center" type="index" label="" width="65"></el-table-column>
        <el-table-column align="left" prop="deviceId" label="ID" min-width="150" max-width="200"
                         :formatter="formatterAddress"></el-table-column>
        <el-table-column align="left" prop="imsi" label="IMSI" min-width="150" max-width="200"
                         :formatter="formatterAddress"></el-table-column>
        <el-table-column align="left" prop="isp" label="" min-width="150" max-width="200"
                         :formatter="formatterAddress"></el-table-column>
        <el-table-column align="left" prop="netType" label="" min-width="150" max-width="200"
                         :formatter="formatterAddress"></el-table-column>
        <el-table-column align="left" prop="uptime" label="" min-width="150" max-width="200"
                         :formatter="formatterAddress" sortable></el-table-column>
      </el-table>
    </div>

    <!---->
    <el-dialog title="" :visible.sync="runningSetBlack" :width="dialogWidth" center>
      <div>
        <el-row v-show="blackList.length > 0">
          <el-col :span="24">
            <el-button size="medium" @click="deleteBlack(sels,1)" :disabled="sels.length == 0"></el-button>
            <span style="margin-left: 20px"> {{sels.length}}  /  {{blackList.length}} </span>
          </el-col>
        </el-row>
        <el-row style="margin-top: 10px">
          <el-col :span="16">
            <el-table :data="blackList" v-loading="listLoading1" class="center-block" stripe
                      @selection-change="handleSelectionChange">
              <el-table-column type="selection" width="55" label=""></el-table-column>
              <el-table-column type="index" width="50" label=""></el-table-column>
              <el-table-column align="left" prop="imsi" label="imsi" min-width="100" max-width="200"
                               :formatter="formatterAddress"></el-table-column>
              <el-table-column align="left" label="" width="80">
                <template slot-scope="scope">
                  <el-button icon="el-icon-close" type="text" size="medium" style="font-size: 22px"
                             @click="deleteBlack(scope.row,2)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
          <el-col :span="8" style="padding-left: 20px">
            <el-form :model="device" :rules="rules">
              <h4 style="text-align: left;margin-top: 0"></h4>
              <el-form-item prop="imsi">
                <el-input v-model="device.imsi" placeholder="" :maxlength="30"></el-input>
              </el-form-item>
              <el-button type="primary" size="medium" @click="addBlack"></el-button>
            </el-form>
          </el-col>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import FileSaver from 'file-saver';
  import XLSX from 'xlsx';
  import {formatDate} from '../../assets/js/util.js';

  export default {
    data() {
      let imsiValidator = (rule, value, callback) => {
        if (!/[0-9.]$/.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      return {
        listLoading1: false,
        runningSetBlack: false,
        dialogWidth: this.$Is_Pc() ? '50%' : '90%',
        options: [{value: '2', label: ''}, {value: '0', label: ''}, {value: '1', label: ''}],
        list: [],
        blackList: [],
        sels: [],
        rules: {
          imsi: [
            {required: true, message: '', trigger: 'blur'}, {validator: imsiValidator, trigger: "change,blur"}
          ]
        },
        device: {imsi: ''},
        intervalid: null
      }
    },
    //
    beforeDestroy() {
      clearInterval(this.intervalid);
    },
    methods: {
      //
      dataTask() {
        if (!this.intervalid) {
          this.intervalid = setInterval(() => {
            this.getData();
          }, 3000);
        }
      },
      //
      getData() {
        let param = {msgId: "b7518c70", type: 4194, cmd: 4528, moduleID: 255, timestamp: new Date().getTime()};
        this.$post(param).then((data) => {
          if ("000000" === data.code) {
            if (data.data.metaInfo && data.data.metaInfo.content.length > 0) {//
              let list = JSON.parse(data.data.metaInfo.content);
              if (list.length > 0) {
                let terminate = JSON.parse(sessionStorage.getItem("terminate"));
                if (terminate) {
                  terminate = terminate.concat(list);
                  sessionStorage.setItem("terminate", JSON.stringify(terminate));
                } else {
                  sessionStorage.setItem("terminate", JSON.stringify(list));
                }
              }
            }
            //
            let datas = JSON.parse(sessionStorage.getItem("terminate"));
            if (datas && datas.length > 0) {
              this.getList(datas);
            }
          }
        }).catch((err) => {//
          this.$message.error(err);
          this.list = [];
          let terminate = JSON.parse(sessionStorage.getItem("terminate"));
          if (terminate && terminate.length > 0) {
            this.getList(terminate);
          }
        });
      },
      //
      getList(datas) {
        this.list = [];
        let bol = JSON.parse(sessionStorage.getItem("black"));
        if (bol && bol.length > 0) {//
          if (datas.length > 0) {
            datas.forEach((item) => {
//              if (this.getIndex(bol, item) >= 0) {//
//                this.list.unshift(item);
//              }
              if (this.getNoIndex(bol, item)) {//
                this.list.unshift(item);
              }
            });
          }
        } else {
//          if (this.list.length > 0) {
//            this.list.concat(datas);
//          } else {
          this.list = datas;
//          }
        }
      },
      //imsiindex
      getNoIndex(arr, item) {
        let index = true;
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].imsi === item.imsi) {
            index = false;
          }
        }
        return index;
      },
      //
      getBlack() {
        this.device = {imsi: ''};
        let bol = JSON.parse(sessionStorage.getItem("black"));
        if (bol) {
          this.blackList = bol;
        } else {
          this.blackList = [];
        }
      },
      //
      deleteBlack(val, flag) {
        this.$confirm('imsi?', '', {type: 'info'}).then(() => {
          let bol = JSON.parse(sessionStorage.getItem("black"));
          if (bol) {
            if (flag === 1) {
              val.forEach((item) => {
                bol.splice(this.getIndex(bol, item), 1);
              });
            } else {
              bol.splice(this.getIndex(bol, val), 1);
            }
          }
          sessionStorage.setItem("black", JSON.stringify(bol));
          this.getBlack();
          this.refreshData();
          this.runningSetBlack = false;
        }).catch(() => {
        });
      },
      //
      addBlack() {
        if (!this.device.imsi) {
          this.$message.error('');
          return;
        }
        this.$confirm('imsi?', '', {type: 'info'}).then(() => {
          let bol = JSON.parse(sessionStorage.getItem("black"));
          let black = {imsi: this.device.imsi};
          if (bol) {
            if (this.getIndex(bol, black) < 0) {//imsi
              bol.unshift(black);
              sessionStorage.setItem("black", JSON.stringify(bol));
            } else {//>=0imsi
              this.$message.error('imsi');
              return;
            }
          } else {
            bol = [];
            bol.push(black);
            sessionStorage.setItem("black", JSON.stringify(bol));
          }
          this.getBlack();
          this.refreshData();
          this.runningSetBlack = false;
        }).catch(() => {
        });
      },
      handleSelectionChange(sels) {
        this.sels = sels;
      },
      //excel
      exportExcel() {
        this.$confirm('?', '', {type: 'info'}).then(() => {
          /* generate workbook object from table */
          let wb = XLSX.utils.table_to_book(document.querySelector('#out-table'));
          /* get binary string as output */
          let wbout = XLSX.write(wb, {bookType: 'xlsx', bookSST: true, type: 'array'});
          try {
            FileSaver.saveAs(new Blob([wbout], {type: 'application/octet-stream'}),
              '-' + formatDate(new Date(), 'yyyyMMddhhmmss') + '.xlsx');
          } catch (e) {
            if (typeof console !== 'undefined') console.log(e, wbout)
          }
          return wbout
        }).catch(() => {
        });
      },
      //   --
      formatterAddress(row, column) {
        if (column.property === 'uptime') {//
          return row.uptime ? formatDate(new Date(row.uptime * 1000), 'yyyy-MM-dd hh:mm:ss') : '--';
        } else if (column.property === 'isp') {//
          return row.isp === 0 ? '' : row.isp === 1 ? '' : row.isp === 2 ? '' : '';
        } else if (column.property === 'netType') {// --> 
          return this.getNetType(row.isp);
        } else {
          return row[column.property] && row[column.property] !== "null" ? row[column.property] : '--';
        }
      },
      getNetType(isp) {
        let moduleId = "";
        switch (isp) {
          case 0:
            moduleId = "CMCC";
            break;
          case 1:
            moduleId = "CMUC";
            break;
          case 2:
            moduleId = "CMTC";
            break;
          default:
            break;
        }
        return moduleId;
      },
      getIndex(arr, item) {
        let index = -1;
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].imsi === item.imsi) {
            index = i;
          }
        }
        return index;
      },
      //
      refreshData() {
        this.list = [];
        let terminate = JSON.parse(sessionStorage.getItem("terminate"));
        if (terminate) {
          this.getList(terminate);
        }
      }
    },
    mounted() {
      let terminate = JSON.parse(sessionStorage.getItem("terminate"));
      if (terminate && terminate.length > 0) {
        this.getList(terminate);
      } else {
        this.getData();
      }
      this.dataTask();
    }
  }
</script>



