<template>
  <div>
    <section class="content">
      <h4 style="text-align: left;margin-top: 0"></h4>
      <el-form label-width="120px" label-position="left" ref="deviceMonitor" :rules="rules" :model="deviceMonitor"
               class="card-width">
        <div class="content add-appdiv">
          <el-form-item label="ID" align="left" required>
            <el-input v-model="deviceMonitor.devId" placeholder="ID" :maxlength="20"
                      class="input-width"></el-input>
          </el-form-item>
          <el-form-item label="" style="text-align: left" required>
            <el-input v-model="deviceMonitor.devName" placeholder="" :maxlength="20"
                      class="input-width"></el-input>
          </el-form-item>
          <el-form-item label="" required align="left">
            <el-cascader expand-trigger="hover" :options="provinceList" v-model="selectedOptions2"
                         :props="props" class="input-width" filterable @change="areaChange">
            </el-cascader>
          </el-form-item>
          <el-form-item label="" align="left">
            <el-input v-model="deviceMonitor.location" placeholder="x/x"
                      :maxlength=50 class="input-width"></el-input>
          </el-form-item>
          <!--<el-form-item label="" required>-->
          <!--<el-row :gutter="20">-->
          <!--<el-col :span="11">-->
          <!--<el-input v-model="deviceMonitor.devpos.longitude" @change="changeLon" @blur="changeLon">-->
          <!--<template slot="prepend"></template>-->
          <!--</el-input>-->
          <!--</el-col>-->
          <!--<el-col :span="11">-->
          <!--<el-input v-model="deviceMonitor.devpos.latitude" @change="changeLat" @blur="changeLat">-->
          <!--<template slot="prepend"></template>-->
          <!--</el-input>-->
          <!--</el-col>-->
          <!--</el-row>-->
          <!--</el-form-item>-->
          <el-form-item label="" required align="left">
            <el-input v-model="deviceMonitor.devpos.height" placeholder="" :maxlength=5
                      @change="checkHeight" class="input-width">
              <template slot="prepend"></template>
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="phoneNumber" style="text-align: left">
            {{deviceMonitor.phoneNumber ? deviceMonitor.phoneNumber : "--"}}
            <!--<el-input v-model="deviceMonitor.phoneNumber" placeholder="" :maxlength=11-->
            <!--@change="changePhone" @blur="changePhone"></el-input>-->
          </el-form-item>
          <el-form-item label="" style="text-align: left;margin: 0">
            {{deviceMonitor.version ? deviceMonitor.version : ""}}
          </el-form-item>
        </div>
        <div class="content add-appdiv">
          <el-form-item label="MAC" prop="mac" style="text-align: left;margin: 0">
            {{deviceMonitor.mac ? deviceMonitor.mac : ""}}
            <!--<el-row v-if="isShow">-->
            <!--<el-col :span="15" align="left">-->
            <!--{{deviceMonitor.mac ? deviceMonitor.mac : ""}}-->
            <!--</el-col>-->
            <!--<el-col :span="9" align="left">-->
            <!--<el-button type="text" @click="isShow = false"></el-button>-->
            <!--</el-col>-->
            <!--</el-row>-->
            <!--<el-row v-else>-->
            <!--<el-col :span="15" align="left">-->
            <!--<el-input placeholder="MAC" @change="changeMAC" :maxlength="17"-->
            <!--v-model="deviceMonitor.mac"></el-input>-->
            <!--</el-col>-->
            <!--<el-col :span="9" align="left" style="padding-left: 10px">-->
            <!--<el-button type="primary" @click="updateMAC()" size="medium"></el-button>-->
            <!--<el-button type="text" @click="cancelMac()"></el-button>-->
            <!--</el-col>-->
            <!--</el-row>-->
          </el-form-item>
        </div>
        <div class="content add-appdiv">
          <h4 style="text-align: left;border-left: #33CCFF 3px solid;margin-top: 0;padding-left: 5px">
            WAN
          </h4>
          <el-form-item label="" align="left">
            <el-radio-group v-model="wanItem" @change="handleWanClick" align="left">
              <el-radio-button :label="tab.type" v-for="tab in wanNames" :key="tab.type">{{tab.name}}
              </el-radio-button>
            </el-radio-group>
          </el-form-item>
        </div>
        <div class="content add-appdiv">
          <h4 style="text-align: left;border-left: #33CCFF 3px solid;margin-top: 0;padding-left: 5px"></h4>
          <el-form-item label="IP" style="margin-left: 20px" prop="serverip" align="left">
            <el-input v-model="deviceMonitor.serverip" placeholder="IP" :maxlength="15"
                      class="input-width"></el-input>
          </el-form-item>
          <el-form-item label="" style="margin-left: 20px" prop="serverport" align="left">
            <el-input v-model="deviceMonitor.serverport" placeholder="" :maxlength="10"
                      class="input-width"></el-input>
          </el-form-item>
          <el-form-item label="APP_ID" style="margin-left: 20px" prop="appId" align="left">
            <el-input v-model="deviceMonitor.appId" placeholder="APP_ID" :maxlength="50"
                      class="input-width"></el-input>
          </el-form-item>
          <el-form-item label="APP_KEY" style="margin-left: 20px" prop="appKey" align="left">
            <el-input v-model="deviceMonitor.appKey" placeholder="APP_KEY" :maxlength="50"
                      class="input-width"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div class="main-footer">
        <el-button type="primary" @click="saveSet"></el-button>
      </div>
      <!--MAC-->
      <div class="popu">
        <el-dialog title="" :visible.sync="runModifyMAC" :width="dialogWidth">
          <section>
            <p style="font-size: 16px;padding-bottom: 20px">MAC</p>
            <el-row>
              <el-col :span="12">
                <el-button @click="modifyMAC()" type="text" class="left"></el-button>
              </el-col>
              <el-col :span="12">
                <el-button @click="cancelMac()" type="text" class="right">
                </el-button>
              </el-col>
            </el-row>
          </section>
        </el-dialog>
      </div>
    </section>
  </div>
</template>

<script>
  import json from '../../assets/city.json';
  import {
    ipValid, numValid, mobileValidator, isMac, isApp, doubleValid,
    longitudeValid, latitudeValid, noSValidator, noValidator
  } from '../../assets/js/api.js'

  export default {
    data() {
      let numValidator = (rule, value, callback) => {
        if (!numValid(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      let ipValidator = (rule, value, callback) => {
        if (!ipValid(value)) {
          callback(new Error("IP"));
        } else {
          callback();
        }
      };
      let appValidator = (rule, value, callback) => {
        if (!isApp(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      let idValidator = (rule, value, callback) => {
        if (noValidator(value)) {
          callback(new Error("id"));
        } else {
          callback();
        }
      };
      let devValidator = (rule, value, callback) => {
        if (noSValidator(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      return {
        runModifyMAC: false,//MAC
        isShow: true,
        wanItem: '3Gnet',
        wanNames: [{name: '3G', type: '3Gnet'}, {name: '', type: 'wired'}],
        deviceMonitor: {devpos: {height: '', longitude: '', latitude: ''}},
        props: {value: 'o', label: 'n', children: 'c'},
        selectedOptions2: [],
        macAddress: "",
        provinceList: json,
        dialogWidth: this.$Is_Pc() ? '380px' : '300px',
        rules: {
//          devId: [
//            {required: true, message: 'ID', trigger: 'blur', maxlength: 20},
//            {validator: idValidator, trigger: "change,blur"}
//          ],
//          devName: [
//            {required: true, message: '', trigger: 'blur', maxlength: 20},
//            {validator: devValidator, trigger: "change,blur"}
//          ],
          serverip: [{required: true, message: 'IP', trigger: 'blur'},
            {validator: ipValidator, trigger: "change,blur"}],
          serverport: [{required: true, message: '', trigger: 'blur'},
            {validator: numValidator, trigger: "change,blur"}],
          appId: [{required: true, message: 'APP_ID', trigger: 'blur'},
            {validator: appValidator, trigger: "change,blur"}],
          appKey: [{required: true, message: 'APP_KEY', trigger: 'blur'},
            {validator: appValidator, trigger: "change,blur"}]
        }
      }
    },
    methods: {
      handleWanClick(val) {
        this.wanItem = val;
        this.setNetType();
      },
      //
      areaChange(value) {
        this.selectedOptions2 = value;
        if (value.length === 1) {
          this.deviceMonitor.provinceCode = value[0];
        } else if (value.length === 2) {
          this.deviceMonitor.provinceCode = value[0];
          this.deviceMonitor.cityCode = value[1];
        } else if (value.length === 3) {
          this.deviceMonitor.provinceCode = value[0];
          this.deviceMonitor.cityCode = value[1];
          this.deviceMonitor.areaCode = value[2];
        } else if (value.length === 4) {
          this.deviceMonitor.provinceCode = value[0];
          this.deviceMonitor.cityCode = value[1];
          this.deviceMonitor.areaCode = value[2];
          this.deviceMonitor.townCode = value[3];
        }
      },
      /** */
      changePhone(val) {
        let isVaild = true;
        if (val.length > 0) {
          if (!mobileValidator(val)) {
            this.$message.error('');
            isVaild = false;
          }
        }
        return isVaild;
      },
      /** mac*/
      changeMAC(val) {
        let isVaild = true;
        if (val.length > 0) {
          if (!isMac(val)) {
            this.$message.error('mac');
            isVaild = false;
          }
        } else {
          this.$message.error('mac');
          isVaild = false;
        }
        return isVaild;
      },
      /**2--*/
      checkHeight(value) {//^[0-9]+(\.[0-9]{1,3})?$
        let isVaild = true;
        if (value.length > 0) {
          if (!doubleValid(value)) {
            this.$message.error(',2');
            isVaild = false;
          }
        }
        return isVaild;
      },
      /** */
      changeLon(value) {
        let isVaild = true;
        if (value.length > 0) {
          if (!longitudeValid(value)) {
            this.$message.error('');
            isVaild = false;
          }
        }
        return isVaild;
      },
      /** */
      changeLat(value) {
        let isVaild = true;
        if (value.length > 0) {
          if (!latitudeValid(value)) {
            this.$message.error('');
            isVaild = false;
          }
        }
        return isVaild;
      },
      /** ID*/
      changeDeviceId(value) {
        let isVaild = true;
        if (value.length > 0) {
          if (noValidator(value)) {
            this.$message.error('id');
            isVaild = false;
          }
        } else {
          this.$message.error('ID');
          isVaild = false;
        }
        return isVaild;
      },
      /** */
      changeDeviceName(value) {
        let isVaild = true;
        if (value.length > 0) {
          if (noSValidator(value)) {
            this.$message.error('');
            isVaild = false;
          }
        } else {
          this.$message.error('');
          isVaild = false;
        }
        return isVaild;
      },
      //
      saveSet() {
        if (this.deviceMonitor.devId.length === 0) {
          this.$message.error('ID');
          return;
        }
        if (this.deviceMonitor.devName.length === 0) {
          this.$message.error('');
          return;
        }
        if (this.selectedOptions2.length === 0 || this.deviceMonitor.location.length === 0) {
          this.$message.error('');
          return;
        }
//        if (this.deviceMonitor.devpos.latitude.length === 0 || this.deviceMonitor.devpos.longitude.length === 0) {
//          this.$message.error('');
//          return;
//        }
        if (this.deviceMonitor.devpos.height.length === 0) {
          this.$message.error('');
          return;
        }
//        if (this.deviceMonitor.devpos.longitude) {
//          if (!this.changeLon(this.deviceMonitor.devpos.longitude)) {
//            return;
//          }
//        }
//        if (this.deviceMonitor.devpos.latitude) {
//          if (!this.changeLat(this.deviceMonitor.devpos.latitude)) {
//            return;
//          }
//        }
        if (this.deviceMonitor.devpos.height) {
          if (!this.checkHeight(this.deviceMonitor.devpos.height)) {
            return;
          }
        }
//        if (this.deviceMonitor.phoneNumber) {
//          if (!this.changePhone(this.deviceMonitor.phoneNumber)) {
//            return;
//          }
//        }
        if (this.deviceMonitor.mac) {
          if (!this.changeMAC(this.deviceMonitor.mac)) {
            return;
          }
        }
        this.$refs['deviceMonitor'].validate((valid) => {
          if (valid) {
            this.$confirm('?', '', {type: 'info'}).then(() => {
              this.deviceMonitor.serverport = parseInt(this.deviceMonitor.serverport + "");
              let param = {
                msgId: "b7518c70", type: 4193, cmd: 4256, moduleID: 255, timestamp: new Date().getTime(),
                data: this.deviceMonitor
              };
              this.$emit('openLoading');
              this.$post(param, "").then((data) => {
                this.$emit('closeLoading');
                if ("000000" === data.code) {
                  this.$emit('showDialog', false, true);
                }
              }).catch((err) => {
                this.$emit('closeLoading');
              });
            }).catch(() => {
            });
          }
        });
      },
      //MAC
      modifyMAC() {
        this.runModifyMAC = false;
        let param = {
          msgId: "b7518c70", type: 4193, cmd: 4522, moduleID: 255, timestamp: new Date().getTime(),
          data: {mac: this.deviceMonitor.mac}
        };
        this.$emit('openLoading');
        this.$post(param, "").then((data) => {
          this.$emit('closeLoading');
          if ("000000" === data.code) {
            this.isShow = true;
            this.macAddress = this.deviceMonitor.mac;
          }
        }).catch((error) => {
          this.$emit('closeLoading');
          this.deviceMonitor.mac = this.macAddress;
          this.isShow = true;
        });
      },
      updateMAC() {
        if (this.deviceMonitor.mac) {
          if (!this.changeMAC(this.deviceMonitor.mac)) {
            return;
          }
        }
        this.runModifyMAC = true;
      },
      cancelMac() {
        this.deviceMonitor.mac = this.macAddress;
        this.runModifyMAC = false;
        this.isShow = true;
      },
      //
      setNetType() {
        let type = (this.wanItem == "wired" ? 0 : 1);
        this.$emit('openLoading');
        let param = {
          msgId: "b7518c70", type: 4193, cmd: 4530, moduleID: 255, timestamp: new Date().getTime(),
          data: {wireless: type}
        };
        this.$post(param, "").then((data) => {
          this.$emit('closeLoading');
        }).catch((err) => {
          this.$emit('closeLoading');
        })
      },
      //
      getNetType() {
        let param = {msgId: "b7518c70", type: 4194, cmd: 4531, moduleID: 255, timestamp: new Date().getTime()};
        this.$post(param).then((data) => {
          if ("000000" === data.code && data.data) {//"wireless": 1/0,	13G0
            this.wanItem = (data.data.wireless == 0 ? "wired" : "3Gnet");
          }
        }).catch((err) => {
          this.$message.error(err);
        });
      },
      //
      getDeviceParam() {
        let param = {msgId: 'b7518c70', type: 4194, cmd: 4521, moduleID: 255, timestamp: new Date().getTime()};
        this.$emit('openLoading');
        this.$post(param).then((data) => {
          this.$emit('closeLoading');
          if ("000000" === data.code) {
            this.macAddress = data.data.mac;
            this.deviceMonitor = data.data;
            if (data.data.townCode) {
              this.selectedOptions2 = [data.data.provinceCode, data.data.cityCode, data.data.areaCode, data.data.townCode];
            } else {
              this.selectedOptions2 = [];
            }
            this.deviceMonitor.devpos.height = data.data.devpos.height.toFixed(2) + '';
            this.deviceMonitor.devpos.longitude = data.data.devpos.longitude + '';
            this.deviceMonitor.devpos.latitude = data.data.devpos.latitude + '';
          }
        }).catch((err) => {
          this.$message.error(err);
          this.$emit('closeLoading');
        });
      }
    },
    mounted() {
      this.getDeviceParam();
      this.getNetType();
    }
  }
</script>
<style scoped>
  @media screen and (min-width: 320px) and (max-width: 799px) {
    .card-width {
      margin-left: 0;
    }

    .input-width {
      width: 80%
    }
  }

  @media screen and (min-width: 800px) {
    .card-width {
      margin-left: 20px;
    }

    .input-width {
      width: 60%
    }
  }
</style>



